body{
  margin: 0;
  height: 100vh;
}

#root{
  height: 100%;
}

.input-div{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

input{
  outline-style: none ;
  border: 1px solid #ccc;
  border-radius: 3px;
  padding: 13px 14px;
  width: 50%;
  font-size: 14px;
  font-weight: 700;
  font-family: "Microsoft soft";
}

input:focus{
  border-color: #66afe9;
  outline: 0;
  -webkit-box-shadow: inset 0 1px 1px rgba(0,0,0,.075),0 0 8px rgba(102,175,233,.6);
  box-shadow: inset 0 1px 1px rgba(0,0,0,.075),0 0 8px rgba(102,175,233,.6)
}

svg{
  height: 38px;
  width: 38px;
  margin-left: 10px;
}

.list{
  padding: 20px;
}

.item{
  margin: 20px 0;
  height: 80px;
  border: 1px solid #ccc;
  border-radius: 3px;
  display: flex;
  justify-content:space-between;
  align-items: center;
}

.item svg{
  padding-right: 10px;
}

.item:hover{
  border-color: #66afe9;
  outline: 0;
  -webkit-box-shadow: inset 0 1px 1px rgba(0,0,0,.075),0 0 8px rgba(102,175,233,.6);
  box-shadow: inset 0 1px 1px rgba(0,0,0,.075),0 0 8px rgba(102,175,233,.6)
}

.item-name{
  padding-left: 10px;
  font-size: 17px;
}

.item-href{
  font-weight:100;
  padding-left: 10px;
}

.detail{
  padding: 20px;
}

.detail-list{
  padding-top: 10px;
  display: flex;
  flex-wrap: wrap;
}

.detail-item{
  border: 1px solid #ccc;
  border-radius: 3px;
  display: flex;
  justify-content:space-around;
  align-items: center;
  width: fit-content;
  padding: 16px;
  margin: 0 10px 10px 0;
}

.detail-item:hover{
  border-color: #66afe9;
  outline: 0;
  -webkit-box-shadow: inset 0 1px 1px rgba(0,0,0,.075),0 0 8px rgba(102,175,233,.6);
  box-shadow: inset 0 1px 1px rgba(0,0,0,.075),0 0 8px rgba(102,175,233,.6)
}